<template>
    <v-card color="white py-5">
        <v-flex md12 class="text-xs-center mb-5">
            <v-btn fab class="recordicon" color="error" size="120" @click="startRecording()" :disabled="recordState"><v-icon size="60" v-html="'mic'">mdi-android</v-icon></v-btn>
            <v-btn fab class="recordicon" color="error" @click="stopRecording()" :disabled="stopState"><v-icon size="60" v-html="'mic_off'">mdi-android</v-icon></v-btn>
        </v-flex>
        <v-flex v-if="timer" md12 class="text-xs-center mb-5">
            <label id="minutes">00</label>
            <label id="colon">:</label>
            <label id="seconds">00</label>
        </v-flex>

        <v-flex v-if="stop" md12 class="text-xs-center">
            <div class="text-center">
                <h2>下載音檔</h2>
                <ul id="recordingslist"></ul>
            </div>
        </v-flex>                
    </v-card>
</template>

<script>
    import "../../record/recorder.js"
    var audio_context;
    var recorder;
    export default {
        data() {
            return {
                translate:[],
                recordState: false,
                stopState: true,
                timer: true,
                stop: false
            }
        },
        created(){

        },
        mounted(){
            try {
                // webkit shim
                window.AudioContext = window.AudioContext || window.webkitAudioContext;
                navigator.getUserMedia = navigator.getUserMedia || navigator.webkitGetUserMedia;
                window.URL = window.URL || window.webkitURL;
                
                audio_context = new AudioContext;
                this.__log('Audio context set up.');
                this.__log('navigator.getUserMedia ' + (navigator.getUserMedia ? 'available.' : 'not present!'));
            } catch (e) {
                alert('No web audio support in this browser!');
            }
            
            navigator.getUserMedia({audio: true}, this.startUserMedia, function(e) {
                this.__log('No live audio input: ' + e);
            });
        },
        methods: {
            startUserMedia(stream) {
                var input = audio_context.createMediaStreamSource(stream);
                this.__log('Media stream created.');
                
                recorder = new Recorder(input);
                this.__log('Recorder initialised.');
            },
            __log(e, data) {
                console.log(e + " " + (data || ''))
            },
            startRecording() {
                recorder && recorder.record();
                this.recordState = true;
                this.stopState = false;
                this.__log('Recording...');

                var minutesLabel = document.getElementById("minutes");
                var secondsLabel = document.getElementById("seconds");
                var totalSeconds = 0;
                setInterval(setTime, 1000);

                function setTime()
                {
                    ++totalSeconds;
                    secondsLabel.innerHTML = pad(totalSeconds%60);
                    minutesLabel.innerHTML = pad(parseInt(totalSeconds/60));
                }

                function pad(val)
                {
                    var valString = val + "";
                    if(valString.length < 2)
                    {
                        return "0" + valString;
                    }
                    else
                    {
                        return valString;
                    }
                }


            },
            stopRecording() {
                recorder && recorder.stop();
                this.recordState = true;
                this.stopState = true;
                this.timer = false;
                this.stop = true;
                this.__log('Stopped recording.');
                
                // create WAV download link using audio data blob
                createDownloadLink();
                recorder.clear();
            }
        },
    }

function createDownloadLink() {
    console.log("dddd")
    recorder.exportWAV(function(blob) {
        console.log("blob:" + blob)
        var recordingslist = document.getElementById("recordingslist");
        var url = URL.createObjectURL(blob);
        console.log("url:" + url)
        var li = document.createElement('li');
        console.log("li:" + li)
        var au = document.createElement('audio');
        var hf = document.createElement('a');

        au.controls = true;
        au.src = url;
        hf.href = url;
        hf.download = new Date().toISOString() + '.wav';
        hf.innerHTML = hf.download;
        li.appendChild(au);
        li.appendChild(hf);
        recordingslist.appendChild(li);
    });
}
</script>
<style>
    #recordingslist{list-style: none; }
    #recordingslist audio { display: block; margin:10px auto; }
    .recordicon{width: 100px;height:100px; }
</style>